html {
	touch-action: manipulation;
}
body {
	position: relative;
	margin: 0;
	padding: 0;
	font-family: $default-font;
	font-size: 16px;
	line-height: 1.6;
	//color: $text-color;
	overflow-x: hidden;
	@media screen and (max-width: 991px) {
		font-size: 14px;
		line-height: 24px;
	}
	@media screen and (max-width: 480px) {
		font-size: 12px;
		line-height: 20px;
	}
}

.container {
	max-width: 1580px;//1540
	width: 100%;
	margin: auto;
	padding: 0 20px;
	@media screen and (max-width: 767px) {
		padding: 0 28px;
	}
}

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	clip: rect(0 0 0 0);
	overflow: hidden;
}

.apple-fix {
	width: 100%;
	position: fixed;
}

.no-scroll {
	overflow: hidden;
}

.img-wrapper {
	display: inline-block;
	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.clearfix {
	&::after {
		content: ".";
		display: block;
		clear: both;
		visibility: hidden;
		line-height: 0;
		height: 0;
	}
}

.wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	width: 100%;
	overflow-x: hidden;
	position: relative;
	.header {
		flex: 0 0 auto;
	}
	main {
		position: relative;
		flex: 1 0 auto;
	}
	.footer {
		flex: 0 0 auto;
	}
}

button {
	cursor: pointer;
}