.header__top-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__top {
  background-image: url("../img/png/header_img.png");
  background-position: center auto;
  padding: 8px 0;

  &-container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
  }
}

.drop-menu {
  width: 240px;
  height: 40px;
  border-radius: 4px;
  border: none;
  text-transform: uppercase;
  font-family: inherit;
  text-align: center;
  background-image: url("../img/svg/arrow_down.svg"), url("../img/svg/window_header.svg");
  background-repeat: no-repeat;
  background-position: right 1rem center, left 1rem center;
}

.header__top-item1 {
  font-family: $default-font;
  font-size: 10px;
  list-style: none;
  background: url("../img/svg/map-marker.svg") left 5px no-repeat;
  height: 100%;
  padding-left: 35px;
}

.header__top-item2 {
  font-family: $default-font;
  font-size: 10px;
  list-style: none;
  background: url("../img/svg/callcenter.svg") left 5px no-repeat;
  height: 100%;
  padding-left: 35px;
}

.header__top-text1 {
  font-weight: 500;
  align-items: center;
  color: #1A2735;
  background-color: inherit;
  border: none;
  background-image: url("../img/svg/arrow_down.svg");
  background-repeat: no-repeat;
  background-position: right 1rem center;
  font-family: inherit;
  font-weight: 500;
  font-size: 12px;
  display: flex;
  align-items: center;
  color: #1A2735;
  width: 107%;
}

.header__top-text {
  font-weight: 500;
  font-size: 12px;
  align-items: center;
}

.header__top-number {
  font-weight: bold;
  font-size: 14px;
  line-height: 20px;
  color: #1A2735;
}

.header__top-btn {
  display: flex;
  height: 40px;
  width: 200px;
  justify-content: center;
  align-items: center;
  background: #33B6FF;
  border-radius: 20px;
  text-transform: uppercase;
  color: #FFFFFF;
  font-size: 11px;
  line-height: 20px;
  font-weight: bold;
  background-image: url('../img/svg/handset.svg');
  background-repeat: no-repeat;
  background-position: 19px 12px;
}

.header__midle {
  padding-top: 13px;
  padding-bottom: 10px;
  border-bottom: 1px solid #d5dbe1;

  &-wrap {
    display: flex;
    justify-content: space-between;
    text-align: center;
  }
}

.header__midle-img1 {
  width: 44px;
  height: 49px;
}

.header__midle-img2 {
  width: 119px;
  height: 40px;
}

.header__midle-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.search {
  position: relative;
}

.submit {
  position: absolute;
  top: 10px;
  left: 24px;
  border: none;
  background-color: transparent;
  background: url("../img/svg/loupe.svg") no-repeat;
  height: 22px;
  width: 22px;
}

.header__midle-input {
  border: 1px solid #c1c9d2;
  box-sizing: border-box;
  border-radius: 90px;
  width: 492px;
  height: 40px;
  outline: none;
  padding-left: 62px;
  padding-right: 15px;

  &::placeholder {
    color: #31455B;
    opacity: 1;
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;

  }
}

.header__midle-btn1,
.header__midle-btn2 {
  display: flex;
  height: 40px;
  width: 200px;
  justify-content: center;
  align-items: center;
  border: 1px solid #405368;
  box-sizing: border-box;
  border-radius: 20px;
  font-family: inherit;
  text-transform: uppercase;
  font-size: 11px;
  font-weight: bold;
  color: #31455B;
}

.header__midle-btn1 {
  background: url("../img/svg/calc.svg") no-repeat;
  background-position: left 2rem center;
  padding-left: 30px;
}

.header__midle-btn2 {
  background: url("../img/svg/tape-line.svg") no-repeat;
  background-position: left 1rem center;
  padding-left: 30px;
}

.header__bottom {
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #d5dbe1;

  &-container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
  }

  &-menu {
    justify-content: space-between;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 24px;
    display: flex;
    align-items: center;
    color: #31455B;
  }
}

.header__breadcrumbs {
  display: flex;
  padding-top: 10px;
  justify-content: flex-start;
  line-height: 16px;
  & li:first-child::before{
    content: "";
    margin-left: -15px;
  }
  & li::before{
      content: '-';
      /* Разделитель */
      margin-left: 15px;
      /* Отступ слева */
      margin-right: 15px;
      /* Отступ справа */
      color: #7f7f7f;
      /* Цвет разделителя */
  }
  &-container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
  }

  &.no-style li {
    color: #31455B;
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
  }

  &.no-style li a {
    color: #c3cad2;
    text-decoration-line: underline;
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
 
  }

}