      font-size: 14px;
      line-height: 24px; } }
  @media screen and (max-width: 480px) {
    body {
      font-size: 12px;
      line-height: 20px; } }

.container {
  max-width: 1580px;
  width: 100%;
  margin: auto;
  padding: 0 20px; }
