    .container {
      padding: 0 28px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.apple-fix {
  width: 100%;
  position: fixed; }

.no-scroll {
  overflow: hidden; }

.img-wrapper {
  display: inline-block; }
  .img-wrapper img {
    width: 100%;
    height: 100%;
    object-fit: cover; }

.clearfix::after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  overflow-x: hidden;
  position: relative; }
  .wrapper .header {
    flex: 0 0 auto; }
  .wrapper main {
    position: relative;
    flex: 1 0 auto; }
  .wrapper .footer {
    flex: 0 0 auto; }

button {
  cursor: pointer; }

.header__top-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header__top {
  background-image: url("../img/png/header_img.png");
  background-position: center auto;
  padding: 8px 0; }
  .header__top-container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto; }

.drop-menu {
  width: 240px;
  height: 40px;
  border-radius: 4px;
  border: none;
  text-transform: uppercase;
  font-family: inherit;
  text-align: center;
  background-image: url("../img/svg/arrow_down.svg"), url("../img/svg/window_header.svg");
  background-repeat: no-repeat;
  background-position: right 1rem center, left 1rem center; }

.header__top-item1 {
  font-family: "Montserrat", sans-serif;
  font-size: 10px;
  list-style: none;
  background: url("../img/svg/map-marker.svg") left 5px no-repeat;
  height: 100%;
  padding-left: 35px; }

.header__top-item2 {
  font-family: "Montserrat", sans-serif;
  font-size: 10px;
  list-style: none;
  background: url("../img/svg/callcenter.svg") left 5px no-repeat;
  height: 100%;
  padding-left: 35px; }

.header__top-text1 {
  font-weight: 500;
  align-items: center;
  color: #1A2735;
  background-color: inherit;
  border: none;
  background-image: url("../img/svg/arrow_down.svg");
  background-repeat: no-repeat;
  background-position: right 1rem center;
  font-family: inherit;
  font-weight: 500;
  font-size: 12px;
  display: flex;
  align-items: center;
  color: #1A2735;
  width: 107%; }

.header__top-text {
  font-weight: 500;
  font-size: 12px;
  align-items: center; }

.header__top-number {
  font-weight: bold;
  font-size: 14px;
  line-height: 20px;
  color: #1A2735; }

.header__top-btn {
  display: flex;
  height: 40px;
  width: 200px;
  justify-content: center;
  align-items: center;
  background: #33B6FF;
  border-radius: 20px;
  text-transform: uppercase;
  color: #FFFFFF;
  font-size: 11px;
  line-height: 20px;
  font-weight: bold;
  background-image: url("../img/svg/handset.svg");
  background-repeat: no-repeat;
  background-position: 19px 12px; }

.header__midle {
  padding-top: 13px;
  padding-bottom: 10px;
  border-bottom: 1px solid #d5dbe1; }
  .header__midle-wrap {
    display: flex;
    justify-content: space-between;
    text-align: center; }

.header__midle-img1 {
  width: 44px;
  height: 49px; }

.header__midle-img2 {
  width: 119px;
  height: 40px; }

.header__midle-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto; }

.search {
  position: relative; }

.submit {
  position: absolute;
  top: 10px;
  left: 24px;
  border: none;
  background-color: transparent;
  background: url("../img/svg/loupe.svg") no-repeat;
  height: 22px;
  width: 22px; }

.header__midle-input {
  border: 1px solid #c1c9d2;
  box-sizing: border-box;
  border-radius: 90px;
  width: 492px;
  height: 40px;
  outline: none;
  padding-left: 62px;
  padding-right: 15px; }
  .header__midle-input::placeholder {
    color: #31455B;
    opacity: 1;
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 13px; }

.header__midle-btn1,
.header__midle-btn2 {
  display: flex;
  height: 40px;
  width: 200px;
  justify-content: center;
  align-items: center;
  border: 1px solid #405368;
  box-sizing: border-box;
  border-radius: 20px;
  font-family: inherit;
  text-transform: uppercase;
  font-size: 11px;
  font-weight: bold;
  color: #31455B; }

.header__midle-btn1 {
  background: url("../img/svg/calc.svg") no-repeat;
  background-position: left 2rem center;
  padding-left: 30px; }

.header__midle-btn2 {
  background: url("../img/svg/tape-line.svg") no-repeat;
  background-position: left 1rem center;
  padding-left: 30px; }

.header__bottom {
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #d5dbe1; }
  .header__bottom-container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto; }
  .header__bottom-menu {
    justify-content: space-between;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 24px;
    display: flex;
    align-items: center;
    color: #31455B; }

.header__breadcrumbs {
  display: flex;
  padding-top: 10px;
  justify-content: flex-start;
  line-height: 16px; }
  .header__breadcrumbs li:first-child::before {
    content: "";
    margin-left: -15px; }
  .header__breadcrumbs li::before {
    content: '-';
    /* Разделитель */
    margin-left: 15px;
    /* Отступ слева */
    margin-right: 15px;
    /* Отступ справа */
    color: #7f7f7f;
    /* Цвет разделителя */ }
  .header__breadcrumbs-container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto; }
  .header__breadcrumbs.no-style li {
    color: #31455B;
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px; }
  .header__breadcrumbs.no-style li a {
    color: #c3cad2;
    text-decoration-line: underline;
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px; }

.hero {
  padding-top: 42px;
  padding-bottom: 19px; }
  .hero__container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto; }
  .hero__title {
    font-family: Open Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 32px;
    margin-bottom: 25px;
    /* identical to box height, or 133% */
    text-transform: uppercase;
    position: relative; }
    .hero__title::before {
      content: "";
      display: block;
      width: 48px;
      height: 4px;
      background: #31455B;
      left: 0;
      top: 0;
      position: absolute;
      margin-top: -14px; }
    .hero__title span {
      font-weight: 900; }
  .hero__text {
    max-width: 779px;
    margin-bottom: 42px; }
  .hero__slider-wrap {
    display: flex;
    justify-content: space-between;
    padding-top: 24px;
    padding-bottom: 24px; }
  .hero__slider-item {
    display: flex;
    flex-direction: column;
    width: 200px;
    align-items: center;
    position: relative; }
    .hero__slider-item input:checked ~ ::before {
      background: #EEF9FF;
      content: "";
      top: 0;
      right: 0;
      bottom: -50px;
      left: 0;
      position: absolute;
      z-index: -1;
      border-radius: 8px 8px 0  0; }
    .hero__slider-item input:checked ~ svg path {
      stroke: #33B6FF; }
    .hero__slider-item input:checked ~ svg rect {
      stroke: #33B6FF; }
    .hero__slider-item input {
      visibility: hidden; }
  .hero__slider-text {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 18px;
    color: #31455B;
    text-align: center;
    margin-top: 20px; }

.hero__slider-text {
  margin-top: 8px !important; }

.hero__slider-paragraph2 {
  margin-top: 3px !important; }

::after,
::before {
  box-sizing: inherit; }

.profile-system {
  padding-top: 1px;
  background: #EEF9FF;
  color: #31455B; }
  .profile-system__wrap {
    display: flex;
    justify-content: space-between;
