.hero {
    padding-top: 42px;
    padding-bottom: 19px;

    &__container {
        width: 100%;
        max-width: 1200px;
        margin: 0 auto;
    }

    &__title {
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 24px;
        line-height: 32px;
        margin-bottom: 25px;
        /* identical to box height, or 133% */
        text-transform: uppercase;
        position: relative;

        &::before {
            content: "";
            display: block;
            width: 48px;
            height: 4px;
            background: #31455B;
            left: 0;
            top: 0;
            position: absolute;
            margin-top: -14px;
        }

        & span {
            font-weight: 900;
        }
    }

    &__text {
        max-width: 779px;
        margin-bottom: 42px;
    }

    &__slider-wrap {
        display: flex;
        justify-content: space-between;
        padding-top: 24px;
        padding-bottom: 24px;
        // overflow: hidden;
    }
    
    &__slider-item {
        display: flex;
        flex-direction: column;
        width: 200px;
        align-items: center;
        position: relative;
        // height: 100%;
        
        & input:checked~::before {
            // color: red;
            background: #EEF9FF;
            content: "";
            top: 0;
            right: 0;
            bottom: -50px;
            left: 0;
            position: absolute;
            z-index: -1;
            border-radius: 8px 8px 0  0;
            
        }

        & input:checked~svg path  {
            stroke: #33B6FF;

        }
        & input:checked~svg rect  {
            stroke: #33B6FF;

        }
       
        // padding-top: 24px;
        // padding-bottom: 24px;
        & input {
            visibility: hidden;
        }


    }

    &__slider-text {
        font-family: Montserrat;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 18px;
        color: #31455B;
        text-align: center;
        margin-top: 20px;
    }
}

