.profile-system {
    padding-top: 1px;
    background: #EEF9FF;
    color: #31455B;

    &__wrap {
        display: flex;
        justify-content: space-between;
        border: 1px solid #D2DEE6;
        border-radius: 8px;
        margin: 0 24px;

    }

    &__text {
        margin: 24px 0 18px 24px;
    }

    &__link {
        width: 288px;
        height: 56px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-right: 1px solid #D2DEE6;

        // &:first-child {
        //     background: #33B6FF;
        //     border-radius:   8px 0 0 8px;
        //     color: #ffffff;
        //     border-right: 1px solid #33B6FF;
        // }

        &:last-child {
            border-right: 0;
        }
    }

}
